<template>
    <div class="reset-container">
        <el-form autoComplete="on" label-position="left"
                 label-width="0px"
                 class="card-box reset-form">
            <div>
                <router-link to="/basic-credit" class="back-icon">
                    首页<i class="el-icon-arrow-right"></i>
                </router-link>
                <h3 class="title">信用分基础</h3>
            </div>
          <el-table
            :data="currList"
            style="width: 100%">
            <el-table-column
              prop="onedayScoreLimit"
              label="单日用户上限总分"
              width="180">
            </el-table-column>
            <el-table-column
              prop="updateTime"
              label="修改时间 "
              width="180">
            </el-table-column>
            <el-table-column
              prop="operator"
              label="操作账号">
            </el-table-column>
             <el-table-column
              prop="status"
              label="状态">
            </el-table-column>
          </el-table>
          <router-link :to="opUrl" >
            <el-button type="primary" style="width:80px;">
                修改
            </el-button>
            </router-link>
            <h3>修改历史记录</h3>
              <el-table
            :data="basicList.items"
            style="width: 100%">
            <el-table-column
              prop="onedayScoreLimit"
              label="单日用户上限总分"
              width="180">
            </el-table-column>
            <el-table-column
              prop="updateTime"
              label="修改时间 "
              width="180">
            </el-table-column>              
            <el-table-column
              prop="updateTime"
              label="下架时间 "
              width="180">
            </el-table-column>
            <el-table-column
              prop="operator"
              label="操作账号">
            </el-table-column>
             <el-table-column
              prop="status"
              label="状态">
            </el-table-column>
          </el-table>
            <div class="block">
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currPage"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="pSize"
      layout="sizes, prev, pager, next, jumper"
      :total="totalNum">
    </el-pagination>
  </div>
</el-form-item>
</el-form>
</div>
</template>

<script>
    import {
        listBasic
    } from 'api/basicCredit';
    export default {
        name: 'reset',
        data() {
            return {
                currPage: 1,
                pSize: 10,
                basicList: [],
                currList: [],
                totalNum: 0,
                opUrl: "/basic-update"
            }
        },
        computed: {},
        created() {
            this.getCurrent();
            this.changeCredit(this.currPage, this.pSize);
        },
        methods: {
            getCurrent() {
                var _vue = this;
                listBasic(1, 10).then(response => {
                    if (response.success) {
                        const data = response.data;
                        if (data.confBaseSet) {
                            const curr = [{             
                                "onedayScoreLimit": data.confBaseSet.onedayScoreLimit == -1 ? "无上限" : data.confBaseSet.onedayScoreLimit,
                                 "updateTime": data.confBaseSet.updateTime,
                                 "status": data.confBaseSet.status == 1 ? "下架" : "正常",
                                 "operator": data.confBaseSet.operator
                            }];
                            _vue.currList = curr;
                            _vue.totalNum = data.count;
                            _vue.opUrl = data.confBaseSet ? ("/basic-update?id=" + data.confBaseSet.id) : "/basic-update";
                        }
                    }
                    resolve();
                }).catch(error => {
                    // reject(error);
                });

            },
            changeCredit(page, size) {
                var _vue = this;
                listBasic(page, size).then(response => {
                    if (response.success) {
                        const data = response.data;
                        data.items.forEach(function(e, i) {
                            e.onedayScoreLimit = e.onedayScoreLimit == -1 ? "无上限" : e.onedayScoreLimit;
                            e.status = e.status == 1 ? "下架" : "正常"
                        });
                        _vue.basicList = data;
                    }
                    resolve();
                }).catch(error => {
                    // reject(error);
                });
            },
            handleSizeChange(val) {
                this.pSize = val;
                this.changeCredit(this.currPage, this.pSize);
            },
            handleCurrentChange(val) {
                this.currPage = val;
                this.changeCredit(this.currPage, this.pSize);
            }
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    @import "src/styles/mixin.scss";
    .el-pagination {
        text-align: right;
    }
    
    .reset-container {
        .el-table {
            margin-top: 20px;
            margin-bottom: 20px;
        }
        input:-webkit-autofill {
            -webkit-box-shadow: 0 0 0px 1000px #293444 inset !important;
            -webkit-text-fill-color: #3E3E3E !important;
        }
        @include relative;
        height: 100vh;
        background-color: #fff;
        .back-icon {
            float: left;
            margin-top: 5px;
        }
        .el-icon-information {
            position: absolute;
            right: -18px;
            top: 10px;
        }
        .reset-form {
            position: absolute;
            left: 0;
            right: 0;
            width: 350px;
            padding: 35px 35px 15px 35px;
        }
        .card-box {
            padding: 20px;
            box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);
            -webkit-border-radius: 5px;
            border-radius: 5px;
            -moz-border-radius: 5px;
            background-clip: padding-box;
            margin-bottom: 20px;
            background-color: #F9FAFC;
            width: 100%;
        }
        .title {
            margin: 0px auto 40px auto;
            text-align: left;
            color: #505458;
        }
    }
</style>


